<template>
  <div>

    <el-row
      type="flex"
      class="row-bg"
      justify="space-around"
    >
      <el-col
        :span="8"
        class="colGroup"
      >

        <h4 style="margin-left: 20px;">ATS组播参数</h4>

        <div style="margin-right: 20px; margin-left: 20px;">

          <el-button
            type="primary"
            @click="initAts"
             :disabled="initFlDisable"
          >
            初始化
          </el-button>

          <el-button
            type="success"
            @click="saveAts()"
            :disabled='atsSaveDisable'
          >
            保存
          </el-button>

          <div style="margin-top:20px">

            <edit-table
              :table_ui_data="atsUiData"
              :table_data="tableData"
              :table_disable_edit="false"
              ref='edtableAts'
               :unique_id="['lineId']"
              @dataChanged="atsDataChanged"
            ></edit-table>

          </div>

        </div>
      </el-col>

      <el-col
        :span="15"
        class="colGroup"
      >

        <h4 style="margin-left: 20px;">首末班车参数</h4>

        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            :inline="true"
            label-position="left"
          >

            <el-form-item label="线路选择">
              <el-select
                v-model="selectLineId"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in lineList"
                  :key="item.id"
                  :label="item.id + ' ' +item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="initFL"
                :disabled="initFlDisable"
              >
                初始化
              </el-button>

              <el-button
                type="success"
                @click="saveFL"
                :disabled='flSaveDisable'
              >
                保存
              </el-button>
            </el-form-item>

          </el-form>

          <div>

            <edit-table
              :table_ui_data="flUiData"
              :table_data="tableData_FL"
             :table_disable_edit="false"
              :unique_id="['name']"
              ref="edtableFl"
              @dataChanged="flDataChanged"
            ></edit-table>
          </div>

        </div>
      </el-col>
      <tipDialog
        @okClick='dialogClick'
        :dialog='dialogOb'
      ></tipDialog>
    </el-row>

  </div>
</template>

<script>
import { Message } from "element-ui";
import tipDialog from "../tipDialog.vue";

export default {
  components: {
    tipDialog,
  },
  data() {
    return {
      atsSaveDisable: true,
      flSaveDisable: true,
      tableData: [],
      initFlDisable:true,
      tableData_FL: [],
      lineList: [],
      selectLineId: "",
     
      dialogOb: { visible: false, title: "提示", tipMsg: "", trsfParm: {} },

      atsUiData: [
        {
          label: "线路",
          prop: "lineId",
          disabled: true,
        },
        { label: "组播地址", prop: "address" },
        { label: "端口", prop: "port" }
      ],
      flUiData: [
        { label: "车站名称", prop: "name", disabled: true },
        { label: "上行方向", prop: "up",uiType:1,selectlist:[]},
        { label: "首班车", prop: "fTrian_up" },
        { label: "末班车", prop: "lTrain_up" },
        { label: "下行方向", prop: "down" ,uiType:1,selectlist:[]},
        { label: "首班车", prop: "fTrian_down" },
        { label: "末班车", prop: "lTrain_down" }
      ],
    };
  },
  watch: {
    selectLineId(newV, oldV) {
      console.log("线路切换", newV, oldV);
      if (!this.flSaveDisable) {
        //提示是否保存
        console.log("待完善");
      }
      //获取对应的信息
      var that = this;
      
      this.$httpApi.lineList(newV).then((response) => {
        if (response.data.code == "0") {
          var selectStaList = response.data.dataList;
          console.log( that.selectStaList);
          selectStaList.forEach(ele=>{
            ele.val = ele.id;
          });
          
          that.flUiData.forEach(ele=>{
              if(ele.uiType)
              {
                  ele.selectlist = selectStaList;
              }
          });

        }
      });
      this.$httpApi.trainInfo(newV).then((response) => {
        if (response.data.code == "0") {
          that.tableData_FL = response.data.list;
          that.$refs.edtableFl.initCurrentData(that.tableData_FL);
        }
      });
    },
  },
  methods: {
    initAtsTableData() {
    
        //获取线路
        this.tableData = new Array();
        this.lineList.forEach((element) => {
          var da = {};
          da.lineId = element.id;
          da.address = "224.3.1.1";
          da.port = "7650";
          da.isEdit = false;
          this.tableData.push(da);
        });
      
    },
    initAts() {
      if (this.tableData && this.tableData.length > 0) {
        this.dialogOb.tipMsg = "初始化将清除列表中的数据,是否继续?";
        this.dialogOb.trsfParm.isAts = true;
        this.dialogOb.visible = true;
      } else {
        this.initAtsTableData();
      }
    },
    saveAts() {
      this.$refs.edtableAts.confirmOKAction((obj, typeNum) => {
        if (typeNum == 1) {
          this.$httpApi.add_atsMc(obj).then((response) => {
            if (response.data.code != 0) {
              Message.error(response.data.message);
            }
          });
        } else if (typeNum == 2) {
          this.$httpApi.up_atsMc(obj).then((response) => {
            if (response.data.code != 0) {
              Message.error(response.data.message);
            }
          });
        }
      });
      
    },
    dialogClick(lparm) {
      if (lparm.isAts) {
        //ats组播初始化
        this.initAtsTableData();
      } else {
        //首末班车
        this.initFSTableData();
      }
    },
    atsDataChanged(va) {
      this.atsSaveDisable = va == 0;
    },
    flDataChanged(va) {
      this.flSaveDisable = va == 0;
    },
   
    initFSTableData() {
     
        //获取线路
        this.tableData_FL = new Array();
        var that = this;
        this.$httpApi.lineList(this.selectLineId).then((response) => {
          if (response.data.code == "0") {
            var da = response.data.dataList;
            if (da) {
              da.forEach((element) => {
                var mp = {};
                mp.name = element.id;
                mp.up = "";
                mp.fTrian_up = "05:00:00";
                mp.lTrain_up = "23:00:00";
                mp.down = "";
                mp.fTrian_down = "05:00:00";
                mp.lTrain_down = "23:00:00";
                mp.isEdit = false;
                that.tableData_FL.push(mp);
              });
            }
          }
        });
     
    },
    initFL() {
      if (this.tableData_FL && this.tableData_FL.length > 0) {
        this.dialogOb.tipMsg = "初始化将清除列表中的数据,是否继续?";
        this.dialogOb.trsfParm.isAts = false;
        this.dialogOb.visible = true;
      } else {
        this.initFSTableData();
      }
    },
    saveFL() {
       this.$refs.edtableFl.confirmOKAction((obj, typeNum) => {
        if (typeNum == 1) {
          this.$httpApi.add_trainInfo(this.selectLineId,obj).then((response) => {
            if (response.data.code != 0) {
              Message.error(response.data.message);
            }
          });
        } else if (typeNum == 2) {
          this.$httpApi.up_trainInfo(this.selectLineId,obj).then((response) => {
            if (response.data.code != 0) {
              Message.error(response.data.message);
            }
          });
        }
      });
      

    },
  },
  mounted() {
    const that = this;

    that.tableData_FL = [];
    this.$httpApi.atsMcList_get().then(function (response) {
      //console.log(response.data);
      if (response.data.code == "0") {
        that.tableData = response.data.list;
        that.$refs.edtableAts.initCurrentData(that.tableData);
      }
    });

    this.$httpApi.lineInfo("0").then(function (response) {
      if (response.data.code == "0") {
        that.lineList = response.data.dataList;
        if (that.lineList && that.lineList.length > 0) {
          that.selectLineId = that.lineList[0].id;
          that.initFlDisable = false;
        }else{
          that.initFlDisable = true;
        }
      }
    });
  },
};
</script>

<style scoped>
.inputNoBorder >>> .el-input__inner {
  -webkit-appearance: none;
  background-color: transparent;
  background-image: none;
  border-radius: 4px;
  text-align: center;
  border: 0px;
  width: 100%;
}
</style>